  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App{
  min-height: 100vh;
  background-color: #39445a;
  color: white;
  padding-top: 40px;
  padding-bottom: 70px;
}

@media (max-width: 700px) {
  .App{
    padding-top: 39px;
  }
}
.header{
  width: 100%;
  cursor: pointer;
  position: fixed;
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  background-color: #39445a;
  font-family: "Montserrat", sans-serif;
  /*font-size: 4vw;*/
  padding-bottom: 5px;
  box-shadow: 0px 1px 5px  black;
  color: white;
  z-index: 100;
}

