.movie{
  height: 100%;
  display: grid;
  grid-auto-flow: row;
  grid-template: 0.001fr minmax(30px,1fr) minmax(10px,auto) minmax(10px,auto)/1fr;
  padding: 5px;
  background-color: #282c34;
  color: white;
  border: 0 !important;
  border-radius: 10px;
  position: relative;
  font-family: "Montserrat",sans-serif;
}
.movie:hover{
  background-color: white;
  color: black;
}
