.content__container{
  object-fit: contain;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
  background-color: #39445A;
  color: #fff;
}
/* хром, сафари */
.content__container::-webkit-scrollbar { width: 0; }
/* ie 10+ */
.content__container { -ms-overflow-style: none; }
/* фф (свойство больше не работает, других способов тоже нет)*/
.content__container { overflow: -moz-scrollbars-none; }


