  border-radius: 10px;
  position: relative;
  /*padding: 0 0 150px 0;*/
  display: flex;
  overflow: hidden;
  flex: 1 1 auto;
}
.back__poster{
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
