.content__carusel{}

.alice-carousel__stage-item{
  max-width: 8rem;
}

.carousel__item{
  display: flex;
  flex-direction: column;
  object-fit: contain;
  padding: 5px 10px;
  max-height: 12rem;
}
.carousel__item_img{
  border-radius: 10px;
  object-fit: cover;
  margin-bottom: 3px;
  overflow: hidden;
  box-shadow: 0px 0px 5px  black;
}
.carousel__item_text{
  font-size: 0.85em;
  line-height: 1em;
  text-transform: capitalize;
  font-weight: 400;
}

@media screen and (max-width: 991px) {
  .carousel__item {
        max-height: 10rem;
    }
}
